h1 {
  color: #fff;
  font-family: "UnifrakturMaguntia";
  font-size: 7rem;
  text-align: center;
  -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: black;
}

h2 {
  padding: 5%;
  font-family: "UnifrakturMaguntia";
}

p {
  font-size: 2rem;
  font-family: "UnifrakturMaguntia";
}

/* navbar */
.navbar-nav {
  padding: 0 100px;
}

.nav-item {
  padding: 0 15px;
}

.dropdown-item, .nav-item {
  font-family: "UnifrakturMaguntia";
  font-size: 1.5rem;
}

.cm-logo {
  border-radius: 50%;
  width: 75%;
}

/* title section */
.banner {
  width: 100%;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* collections list */


.collections {
  display: block;
  padding: 10%;
  margin: auto;
}

.collections-heading {
  text-align: center;
  font-size: 3rem;
}

.c-list-links {
  margin-bottom: 100px;
}

/* instagram */

#instagram {
  background-color: #28282B;
  padding: 100px 0;

}

.market-img {
  border-radius: 2%;
  border: 5px solid black;
  display: block;
  margin: auto;
}

.insta-row {
  margin-right: 0;
}

.insta-header {
  color: #fff;
  font-size: 3rem;
  text-align: left;
  margin-top: 75px;
  padding-left: 0;
}

.insta-para {
  color: #fff;
  width: 80%;
}

.btn {
  font-family: "UnifrakturMaguntia";
}
/* footer */

.copyright-icon {
  font-family: sans-serif;
  font-size: 1rem;
}

/* shipping//refund page */
.shipping-heading{
  font-size: 3rem;
  padding: 5% 2%;
  text-align: center;
}
.shipping-faq {
  font-size: 1.5rem;
  margin: 0 0 3% 20%;
  width: 60%;
}

.back-button {
  color: black;
  display: block;
  font-family: "UnifrakturMaguntia";
  font-size: 1.5rem;
  text-align: center;
}

.back-button:hover {
  color: #36454F;
  text-decoration: none;
}

/* customs page */

.customs-heading {
  font-size: 3rem;
  padding: 5% 0;
}

.customs-content {
  text-align: center;
}

/* login */

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.my-2 {
  margin-top:  calc(1rem);
  margin-bottom: calc(1rem);
}

/* media */
@media (max-width: 1028px) {
  /* homepage */
  h1 {
    font-size: 10rem;
  }

  .nav-link, .dropdown-item {
    font-size: 2.5rem;
  }

  .centered {
    position: relative;
    top: -400px;
    margin-bottom: -350px;
  }


  .insta-content {
    text-align: center;
    margin: auto;
  }

  .insta-button {
    display: block;
    margin: auto;
    font-size: 2.5rem;
    margin-top: 5%;
  }

  /* shipping/returm */

  .shipping-faq {
    width: 80%;
    margin: 0 0 3% 10%;
  }

}
